import React, {useState} from 'react'

import {MenuIcon, XIcon} from '@heroicons/react/outline'
import amitLogo from '../assets/amitc-logo-white.png' 

const Navbar = () => {
    const [nav, setNav] = useState(false)
    const handleClick = () => setNav(!nav)

    const [isMenuVisible, setMenuVisibility] = useState(false);

    const handleTouch = () => {
        setMenuVisibility(!isMenuVisible);
    };

  return (
    <div onTouchStart={handleTouch} className='w-screen h-[80px] z-20 fixed drop-shadow-lg gradient-one'>
        <div className='px-4 md:px-12 flex justify-between items-center w-full h-full'>
            <div className='flex items-start'>
                <img src={amitLogo} className='w-32 sm:w-36' alt="" />
            </div>
            <div className='flex items-end navbar'>
                <ul className='hidden md:flex items-end text-white text-xl'>
                    <li>
                        <a href='/'>Home</a>
                    </li>
                    <li>
                        <a href='/about'>About</a>                    
                    </li>
                    <li>
                    <a href='/services'>Services</a>
                    </li>
                    <li>
                    <a href='/blog'>Blog</a>
                    </li>
                </ul>
            </div>
            <div className='hidden md:flex'>
                <button data-tally-open="31K2lM" data-tally-emoji-text="👋" data-tally-emoji-animation="wave" data-tally-auto-close="3000">Request a Consultation</button>
            </div>
            <div className='md:hidden' onClick={handleClick}>
                {!nav ? <MenuIcon className='w-6 text-white'/> : <XIcon className='w-6 text-white' />}
            </div>
        </div>

        <div className='navbar'>
        <ul className={!nav ? 'hidden' : 'navbar absolute w-full px-4 py-2 bg-indigo-400 bg-opacity-90 text-white text-md font-bold'}>
            <li>
                <a href='/'>Home</a>
            </li>
            <li>
                <a href='/about'>About Apex</a>                    
            </li>
            <li>
            <a href='/services'>Services</a>
            </li>
            <li>
            <a href='/services'>Store</a>
            </li>
            <li>
            <a href='/blog'>Blog</a>
            </li>
            <li>
                <a href='/contact'>Contact</a>
            </li>
            <div className='flex flex-col mb-4 px-4'>
            <button data-tally-open="31K2lM" data-tally-emoji-text="👋" data-tally-emoji-animation="wave" data-tally-auto-close="3000">Request a Consultation</button>
            </div>
        </ul>
        </div>
  </div>
  
  )
}

export default Navbar