import React from 'react'

import inspCCTV from '../assets/inspiration-cctv.png'
import inspIT from '../assets/inspiration-it.jpg'
import inspRetail from '../assets/inspiration-retail.jpg'

    // Import Swiper React components
    import { Swiper, SwiperSlide } from 'swiper/react';

    // Import Swiper styles
    import 'swiper/css';
    import 'swiper/css/pagination';
    import 'swiper/css/navigation';

    // import required modules
import { Keyboard, Pagination, Navigation } from 'swiper/modules';

const Inspiration = () => {
    return (
    <>
      <Swiper name='beinspired'
        slidesPerView={1}
        spaceBetween={30}
        keyboard={{
            enabled: true,
          }}
        loop={true}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Keyboard, Pagination, Navigation]}
        className="mySwiper"
      >
        <SwiperSlide>
            <div>
                <div className="mx-auto max-w-screen-2xl px-4 py-16 sm:px-6 lg:px-8">
                    <div className="grid grid-cols-1 lg:h-screen lg:grid-cols-2">
                        <div className="relative z-10 lg:py-16">
                            <div className="relative h-64 sm:h-80 lg:h-full">
                            <img
                                alt="House"
                                src={inspCCTV}
                                className="absolute inset-0 h-full w-full object-cover rounded-l-md"
                            />
                            </div>
                        </div>

                        <div className="relative flex items-center bg-indigo-100 rounded-md">
                            
                            <div className="p-8 sm:p-16 lg:p-24">
                            <h2 className="text-2xl font-bold sm:text-3xl">
                                Ensuring the safety of your business and customers, one camera at a time.
                            </h2>

                            <p className="mt-4 sm:text-xl text-gray-800">
                                Our CCTV has been pivotal in enhancing safety for various businesses in urban areas. 
                                By strategically placing high-tech cameras and alarm systems, our surveillance has created 
                                a formidable network that deters crime and fosters 
                                a sense of security for both businesses and customers.
                            </p>

                            <button
                                className="w-full sm:w-auto bg-indigo-500 text-white sm:text-2xl mt-8 inline-block"
                                type='button' 
                                    onClick={(e) => 
                                    {e.preventDefault(); 
                                    window.open('https://wa.me/2348173487640?text=I%20need%20an%20IT%20solution%20for%20my%20business.%20How%20do%20I%20begin?')}}
                            >
                                Enhance business safety
                            </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div> 
        </SwiperSlide>
        <SwiperSlide>
            <div>
                <div className="mx-auto max-w-screen-2xl px-4 py-16 sm:px-6 lg:px-8">
                    <div className="grid grid-cols-1 lg:h-screen lg:grid-cols-2">
                    <div className="relative z-10 lg:py-16">
                        <div className="relative h-64 sm:h-80 lg:h-full">
                        <img
                            alt="House"
                            src={inspIT}
                            className="absolute inset-0 h-full w-full object-cover rounded-l-md"
                        />
                        </div>
                    </div>

                    <div className="relative flex items-center bg-indigo-100 rounded-md">
                        
                        <div className="p-8 sm:p-16 lg:p-24">
                        <h2 className="text-2xl font-bold sm:text-3xl">
                            Excellence in IT Hardware, software and robust networking infrastructure.
                        </h2>

                        <p className="mt-4 sm:text-xl text-gray-800">
                            Be assured of consistent delivery of quality hardware equipment, 
                            software that meets current needs and future requirements
                            and a commitment to excellent networking technology for robust communication. Enjoy 
                            seamless operations and improved efficiency.
                        </p>

                        <button
                            className="w-full sm:w-auto bg-indigo-500 text-white sm:text-2xl mt-8 inline-block"
                            type='button' 
                                onClick={(e) => 
                                {e.preventDefault(); 
                                window.open('https://wa.me/2348173487640?text=I%20need%20an%20IT%20solution%20for%20my%20business.%20How%20do%20I%20begin?')}}
                        >
                            Upgrade your business
                        </button>
                        </div>
                    </div>
                    </div>
                </div>
            </div> 
        </SwiperSlide>
        <SwiperSlide>
        <div>
        <div className="mx-auto max-w-screen-2xl px-4 py-16 sm:px-6 lg:px-8">
            <div className="grid grid-cols-1 lg:h-screen lg:grid-cols-2">
            <div className="relative z-10 lg:py-16">
                <div className="relative h-64 sm:h-80 lg:h-full">
                <img
                    alt="House"
                    src={inspRetail}
                    className="absolute inset-0 h-full w-full object-cover rounded-l-md"
                />
                </div>
            </div>

            <div className="relative flex items-center bg-indigo-100 rounded-md">
                
                <div className="p-8 sm:p-16 lg:p-24">
                <h2 className="text-2xl font-bold sm:text-3xl">
                    Keep your eyes on the numbers that matter to your retail business.
                </h2>

                <p className="mt-4 sm:text-xl text-gray-800">
                    Retail technologies help businesses stay ahead by ensuring sales are performed easily and without error,
                    accurate records and inventory are kept and also customer info and relations are properly
                    managed. All while producing data for better business and profitable decision making.
                </p>

                <button className="w-full sm:w-auto bg-indigo-500 text-white sm:text-2xl mt-8 inline-block"
                    type='button' 
                        onClick={(e) => 
                        {e.preventDefault(); 
                        window.open('https://wa.me/2348173487640?text=I%20need%20an%20IT%20solution%20for%20my%20business.%20How%20do%20I%20begin?')}}>
                        Boost your retail
                </button>

                </div>
            </div>
            </div>
        </div>
    </div> 
        </SwiperSlide>
      </Swiper>
    </>
  )
}

export default Inspiration